
    <template>
      <section class="market-doc-container">
        <h2>Badge 徽章</h2>
<h3>基本使用</h3>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">  &lt;template&gt;
    &lt;div&gt;
      &lt;mku-badge :value=&quot;8&quot; class=&quot;doc-mr20&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge :value=&quot;8&quot; class=&quot;doc-mr20&quot;&gt;
        &lt;mku-button type=&quot;primary&quot;&gt;Hello Mku!&lt;/mku-button&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge :value=&quot;8&quot; class=&quot;doc-mr20&quot;&gt;
        &lt;a href=&quot;&quot;&gt;这是一个链接&lt;/a&gt;
      &lt;/mku-badge&gt;
    &lt;/div&gt;
  &lt;/template&gt;
</code></pre></template></mku-example><h3>圆点</h3>
<p>支持自定义圆点的大小尺寸。</p>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">  &lt;template&gt;
    &lt;div&gt;
      &lt;mku-badge dot&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge dot type=&quot;info&quot; :dot-size=&quot;20&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge dot type=&quot;warning&quot; dot-size=&quot;20px&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge dot type=&quot;success&quot; dot-size=&quot;30%&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
    &lt;/div&gt;
  &lt;/template&gt;
</code></pre></template></mku-example><h3>展示字符串</h3>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">  &lt;template&gt;
    &lt;div&gt;
      &lt;mku-badge value=&quot;hot&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge value=&quot;new&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
    &lt;/div&gt;
  &lt;/template&gt;
</code></pre></template></mku-example><h3>最大值</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">  &lt;template&gt;
    &lt;div&gt;
      &lt;mku-badge :value=&quot;1000&quot; :max=&quot;99&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
    &lt;/div&gt;
  &lt;/template&gt;
</code></pre></template></mku-example><h3>偏移量</h3>
<mku-example>
        
        <template slot="source"><element-demo4 /></template>
        <template slot="highlight"><pre v-pre><code class="html">  &lt;template&gt;
    &lt;div&gt;
