  &lt;/template&gt;
</code></pre></template></mku-example><h3>自定义颜色</h3>
<mku-example>
        
        <template slot="source"><element-demo5 /></template>
        <template slot="highlight"><pre v-pre><code class="html">  &lt;template&gt;
    &lt;div&gt;
      &lt;mku-badge color=&quot;#e80e9b&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge color=&quot;#ffe58f&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge color=&quot;#0e3de8&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge color=&quot;#0ee829&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge color=&quot;#0ec0e8&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
      &lt;mku-badge color=&quot;#940ee8&quot;&gt;
        &lt;a href=&quot;&quot; class=&quot;doc-badge__link&quot;&gt;&lt;/a&gt;
      &lt;/mku-badge&gt;
    &lt;/div&gt;
  &lt;/template&gt;
</code></pre></template></mku-example><h3>Badge Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:center">默认值</th>
<th style="text-align:center">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">徽章的内容</td>
<td style="text-align:center"><code>String | Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>0</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">max</td>
<td style="text-align:left">定义<code>value</code>为数字类型时的最大值，超出会显示+号</td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">hidden</td>
<td style="text-align:left">是否隐藏徽章</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">hidden-zero</td>
<td style="text-align:left"><code>value</code>为数字类型时，且值为<code>0</code>的时候是否隐藏徽章</td>
<td style="text-align:center"><code>Function</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">dot</td>
<td style="text-align:left">仅展示圆点</td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>false</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">dot-size</td>
<td style="text-align:left">自定义圆点的大小尺寸，为<code>Number</code>类型时单位为<code>px</code></td>
<td style="text-align:center"><code>String | Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">offset</td>
<td style="text-align:left">定义当前徽章中心点在<code>[x、y]</code>轴上的偏移量</td>
<td style="text-align:center"><code>Array</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">几种常见的颜色类型</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">color</td>
<td style="text-align:left">自定义徽章的颜色，权重高于type</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>

      </section>
